* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

:global html,
:global body,
:global #root {
  width: 100%;
  height: 100%;
  min-width: 1920px;
  min-height: 1080px;
  background: #283644;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Helvetica, 'PingFang SC', 'Hiragino Sans GB', 'Microsoft YaHei', SimSun, sans-serif; }

:global li {
  list-style: none; }

:global #container {
  min-width: 100%;
  min-height: 300px; }
